import React, { useEffect, useState } from "react";
import { findNearbyLocations } from "../FindNearbyLocations.jsx";
import LocationCard from "../LocationCards.jsx";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
import { Link } from "react-router-dom";
import Img from "react-cool-img";

const LocationsNearby = () => {
  const [locations, setLocations] = useState();

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async (match, search, showError) => {
    // get users position
    var options = {
      enableHighAccuracy: true,
      timeout: 5000,
      maximumAge: 0,
    };

    const success = async (pos) => {
      console.log("succes");
      var crd = pos.coords;
      const _locations = await findNearbyLocations(crd.latitude, crd.longitude);
      setLocations(_locations);
    };

    const error = async (err) => {
      console.log("err");
      fetch("https://ipapi.co/json")
        .then((res) => res.json())
        .then(async (location) => {
          const _locations = await findNearbyLocations(
            location.latitude,
            location.longitude
          );
          console.log(location, _locations);
          setLocations(_locations);
        });
    };

    navigator.geolocation.getCurrentPosition(success, error, options);
  };

  return (
    <div className="">
      <h2 className="text-black mb-4">Locaties in de buurt</h2>

      {locations &&
        locations.map((location) => {
          return (
            <div
              key={location.id}
              className={`relative overflow-hidden rounded shadow hover:shadow-lg transition-shadow duration-500 ease-in-out`}
            >
              <Link
                className="absolute w-full h-full top-0 left-0 z-10"
                to={`/fotolocatie/${location.slug}`}
              ></Link>

              <Img
                className={`block max-w-none w-18 h-18 object-cover`}
                src={location.photos
                  .sort((a, b) => b.likes - a.likes)[0]
                  .photo[0].url.replace(/-original|-watermark/gi, "-small")}
                alt={`Bekijk locatie ${location.title}`}
              />
            </div>
          );
        })}
    </div>
  );
};

export default LocationsNearby;
